<template>
	<div class="exchange">
		<pubilc-title text=""></pubilc-title>
		<div class="content">
			<div class="contentBox">
					<p>pragma solidity &gt;=0.5.0 &lt;0.6.0;</p>
					<p><br></p>
					<p>contract OrderManagerStorage is OrderManagerStruct, KStorage {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; Order[] public orders;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; mapping( address =&gt; UserInfo) public userInfoMapping;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; uint public depositedUSDMaxLimit = 100000e6;</p>
					<p>&nbsp; &nbsp; uint public exchangeRateUSDToMC = 50;</p>
					<p>&nbsp; &nbsp; uint public costProp = 0.02e12;</p>
					<p>&nbsp; &nbsp; uint public depositTimeInterval = 3 days;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; mapping( uint =&gt; uint ) public depositedLimitMapping;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; mapping( address =&gt; uint[] ) public orderIndexMapping;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; iERC20 internal _usdtInterface;</p>
					<p>&nbsp; &nbsp; iERC777_1 internal _mctInterface;</p>
					<p>&nbsp; &nbsp; Relations internal _relationInterface;</p>
					<p>&nbsp; &nbsp; Manager internal _managerInterface;</p>
					<p>&nbsp; &nbsp; Pool internal _astPoolInterface;</p>
					<p>&nbsp; &nbsp; Compensate internal _compensateInterface;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; _ERC20AssetPool[] internal _hiddenUSDTPools;</p>
					<p><br></p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; mapping(address =&gt; uint) public validCountOf;</p>
					<p>&nbsp; &nbsp; mapping(address =&gt; bool) public isValid;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; struct Invest {address owner; uint amount;}</p>
					<p>&nbsp; &nbsp; struct LuckyDog {uint award; uint time; bool canwithdraw;}</p>
					<p>&nbsp; &nbsp; event Log_Luckdog(address indexed who, uint indexed awardsTotal, uint seqNo);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; bool public isInCountdowning = false;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; uint public deadlineTime;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; bool public death = false;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; uint public withdrawableTotal;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; Invest[] public investQueue;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; mapping(address =&gt; LuckyDog) internal _luckydogMapping;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; constructor(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; iERC20 _usdtInc,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; iERC777_1 _mctInc,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Relations _relationInc,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Pool _poolInc,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Compensate _compInc</p>
					<p>&nbsp; &nbsp; ) public {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _usdtInterface = _usdtInc;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _mctInterface = _mctInc;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _relationInterface = _relationInc;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _astPoolInterface = _poolInc;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _compensateInterface = _compInc;</p>
					<p>&nbsp; &nbsp; }</p>
					<p>}</p>
					<p><br></p>
					<p>pragma solidity &gt;=0.5.0 &lt;0.6.0;</p>
					<p><br></p>
					<p>contract OrderManagerImpl_Order is OrderManagerInternal {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; function createOrder(uint total, uint amount) external whenAlive {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; uint dayz = timestemp() / 1 days * 1 days;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; uint senderOrderCount = orderIndexMapping[msg.sender].length;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; require(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _relationInterface.getIntroducer(msg.sender) != address(0) &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; depositedLimitMapping[dayz] + total &lt;= depositedUSDMaxLimit
						&amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; total &gt;= 100e6 &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; total &lt;= 10000e6 &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; total &lt;= (senderOrderCount + 1) * 2000e6 &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; total % 10e6 == 0 &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; amount &gt;= total * 60 / 100,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; "InvalidParams"</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; if ( senderOrderCount &gt; 0 ) {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; Order storage latestOrder =
						orders[orderIndexMapping[msg.sender][senderOrderCount - 1]];</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; require( timestemp() -
						latestOrder.times[uint8(TimeType.OnCreated)] &gt;= depositTimeInterval, "TimeNotUp");</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; require( latestOrder.total &gt;= 1000e6 ? total &gt;=
						latestOrder.total : total &gt; latestOrder.total );</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _requireTransferUSDTIn(amount);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; depositedLimitMapping[dayz] += total;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; orders.push(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; Order(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; msg.sender,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; orderIndexMapping[msg.sender].length,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; total,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; amount,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 0,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; amount &gt;= total ? OrderStates.Frozen :
						OrderStates.PaymentPart,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; false</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; )</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; orderIndexMapping[msg.sender].push(orders.length - 1);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Order storage o = orders[orders.length - 1];</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if ( o.state == OrderStates.Frozen ) {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; o.times[uint8(TimeType.OnPaymentSuccess)] = timestemp();</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; o.profix = o.total * 1.1e12
						/ 1e12;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; o.times[uint8(TimeType.OnCreated)] = timestemp();</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; o.times[uint8(TimeType.OnCountDownStart)] = timestemp() + 2 days;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; o.times[uint8(TimeType.OnCountDownEnd)] = timestemp() + (2 days + 3 days);</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; o.times[uint8(TimeType.OnUnfreezing)] = timestemp() + (2 days + 5 days) + (o.index
						&gt; 25 ? 25 : o.index) * 1 days;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; userInfoMapping[msg.sender].totalIn += amount;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _pushInvest(msg.sender, amount);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _markValid(msg.sender, total);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if (senderOrderCount == 0) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;_increaseAward(msg.sender,
						_relationInterface.getIntroducer(msg.sender), AwardType.Recommend, amount * 0.1e12 / 1e12, true);</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p>&nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; function orderPayment(uint orderId, uint amount) external whenAlive {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Order storage order = orders[orderId];</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; require( timestemp() &lt;
						order.times[uint8(TimeType.OnCountDownEnd)], "CountDownOver" );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; require( order.owner != address(0), "InvalidOrderID" );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; require( order.state == OrderStates.PaymentPart, "StateError" );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; order.paymented += amount;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; userInfoMapping[order.owner].totalIn += amount;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _requireTransferUSDTIn(amount);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if ( order.paymented &lt; order.total ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; return ;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; order.times[uint8(TimeType.OnPaymentSuccess)] = timestemp();</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; order.state = OrderStates.Frozen;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; uint diffDays =
						(timestemp() - order.times[uint8(TimeType.OnCreated)]) / 1 days;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; uint forzonProp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if ( diffDays &lt;= 2 ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; forzonProp = 1.10e12;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; } else if ( diffDays == 3 ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; forzonProp = 1.08e12;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; } else if ( diffDays == 4 ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; forzonProp = 1.06e12;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; } else {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; forzonProp = 0;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; order.profix = order.total * forzonProp / 1e12;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _pushInvest(msg.sender, amount);</p>
					<p>&nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; function orderSettlement(uint orderId) external whenAlive {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Order storage order = orders[orderId];</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; address owner = order.owner;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; require(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; order.state == OrderStates.Frozen &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; timestemp() &gt; order.times[uint8(TimeType.OnUnfreezing)],</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; "InFrozenning"</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _mctInterface.operatorBurn(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; owner,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; order.profix * costProp / 1e12 * exchangeRateUSDToMC,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; "",</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; "GetHelpBurn"</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; uint40[15] memory props = [</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 0.20e12, 0.15e12, 0.10e12,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 0.03e12, 0.03e12, 0.03e12,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 0.03e12, 0.03e12, 0.03e12,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 0.03e12, 0.03e12, 0.03e12,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; 0.10e12, 0.15e12, 0.20e12</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; ];</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; require( order.profix &gt; order.total, "P&lt;T");</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; uint sp = order.profix - order.total;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; (</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; address[] memory forefathers,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; uint8[] memory levels</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; ) = _relationInterface.getForefathers(owner, 15);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; for ( uint i = 0; i &lt; forefathers.length; i++ ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if (</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; forefathers[i] != address(0) &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; (</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; validCountOf[forefathers[i]] &gt;= (i +
						1) ||</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; validCountOf[forefathers[i]] &gt;= 6 ||</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; levels[i] &gt; 0</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; )</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _increaseAward(msg.sender, forefathers[i],
						AwardType.Manager, sp * props[i] / 1e12, true);</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; order.state = OrderStates.Settlemented;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if ( !_shouldTransferUSDTOut(order.profix) ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _countDownBegin();</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; function orderWithdraw(uint orderId) external whenAlive returns (bool) {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Order storage order = orders[orderId];</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; require(order.state == OrderStates.Settlemented,
						"NeedSettlement");</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; uint len = orderIndexMapping[order.owner].length;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; Order storage latestOrder = orders[orderIndexMapping[order.owner][len - 1]];</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; /* require( latestOrder.times[uint8(TimeType.OnCreated)] / 1 days * 1 days ==
						timestemp() / 1 days * 1 days, "UnRelation"); */</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; /* require( !latestOrder.consumed, "AlreadyConsumend" ); */</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; require( latestOrder.index &gt; order.index, "UnRelation" );</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; if ( !_shouldTransferUSDTOut(order.profix) ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; return false;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; latestOrder.consumed = true;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; order.state = OrderStates.Done;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; order.times[uint8(TimeType.OnDone)] = timestemp();</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; userInfoMapping[order.owner].totalOut += order.profix;</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; _requireTransferUSDTOut(order.owner, order.profix);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;
						(</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; address[] memory addresses,</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; uint[] memory awards</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; ) = _managerInterface.calculationAwards(order.owner, order.total);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; for (uint i = 0; i &lt; addresses.length; i++) {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if ( addresses[i] == address(0) || awards[i] == 0 ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; continue;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; _increaseAward(order.owner, addresses[i], AwardType.Admin, awards[i],
						true);</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; return true;</p>
					<p>&nbsp; &nbsp; }</p>
					<p><br></p>
					<p>&nbsp; &nbsp; function conditionLevelOneFinished(address owner) external view returns (bool) {</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; if (</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; userInfoMapping[owner].totalIn &gt;= 10000e6 &amp;&amp;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; validCountOf[owner] &gt;= 10</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; uint gtCount = 0;</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; address[] memory children = _relationInterface.recommendList(owner);</p>
					<p><br></p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; for ( uint i = 0; i &lt; children.length; i++ ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; if ( userInfoMapping[children[i]].awardQuota &gt;=
						2000e6 ) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;if (++gtCount &gt;= 3) {</p>
					<p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; return true;</p>
